"use client";

import { useEffect, useState } from "react";
import Image from "next/image";
import Link from "next/link";
import { RxHamburgerMenu } from "react-icons/rx";

export default function Header() {
  const [showMainHeader, setShowMainHeader] = useState(true);
  const [scrollY, setScrollY] = useState(0);

  useEffect(() => {
    const handleScroll = () => {
      const currentY = window.scrollY;
      setShowMainHeader(currentY < scrollY || currentY < 50); // Show if scrolling up or near top
      setScrollY(currentY);
    };

    window.addEventListener("scroll", handleScroll);

    return () => window.removeEventListener("scroll", handleScroll);
  }, [scrollY]);

  return (
    <>
      {/* Original Header */}
      <div
        id="headicons"
        className={`fixed top-0 w-full z-50 transition-transform duration-300 ${
          showMainHeader ? "translate-y-0" : "-translate-y-full"
        }`}
        style={{ scrollBehavior: "smooth" }}
      >
        <div className="flex justify-between items-center px-4 bg-white shadow-md">
          <Link href="/">
            <Image
              src="/images/PinkMirror-Favicon.png"
              alt="PinkMirror Logo"
              width={70}
              height={70}
              style={{ margin: "11px 0" }}
            />
          </Link>

          <div
            className="page-title-icon shadow-xl text-white rounded-full p-2 mx-2"
            style={{
              marginTop: "27px",
              backgroundColor: "rgb(247, 148, 29)",
            }}
            data-menu="menu-main"
          >
            <RxHamburgerMenu />
          </div>
        </div>
      </div>

      {/* Sticky Secondary Header */}
      {!showMainHeader && (
        <div className="fixed top-0 w-full z-40 bg-orange-400 text-white p-4 shadow-lg transition-opacity duration-300">
          <div className="flex justify-between items-center">
            <span className="text-lg font-semibold">PinkMirror</span>
            <RxHamburgerMenu size={24} />
          </div>
        </div>
      )}
    </>
  );
}
